@import url("https://fonts.googleapis.com/css2?family=Aldrich&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*:after, *:before {
  box-sizing: border-box;
}

html, body {
  overflow-x: hidden;
}

body {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  background-color: red;
}

button, input, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

img, video, canvas, svg {
  max-width: 100%;
}

/************* variables .... *********/
.loader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100vh;
  width: 100%;
  z-index: 100;
  background: radial-gradient(circle at -8.9% 51.2%, #020024 0%, #090979 15.9%, #0b07e2 15.9%, #00000c 24.4%, #0b134f 24.5%, #0b134f 66%);
  display: grid;
  place-items: center;
  font-family: "Aldrich", sans-serif;
}
@media (max-width: 600px) {
  .loader {
    background: linear-gradient(-79deg, #020024 0%, #090979 15.9%, #0b07e2 15.9%, #020024 24.4%, #0b134f 24.5%, #0b134f 66%);
  }
}
.loader__box {
  position: relative;
  width: 500px;
  height: 200px;
  border-radius: 2px;
  border: 3px solid #01460f;
  border-top: 3px solid #03811a;
  border-bottom: 3px solid #03c026;
  animation: borderAnimation 1s infinite;
}
@keyframes borderAnimation {
  0% {
    border-color: #01460f;
  }
  50% {
    border-color: #03811a;
  }
  100% {
    border-color: #03c026;
  }
}
@media (max-width: 600px) {
  .loader__box {
    width: 90vw;
  }
}
.loader__bar {
  width: 90%;
  height: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%);
  background-color: #ccc;
  border-radius: 2px;
}
.loader__bar--inner {
  height: 100%;
  width: 0;
  border-radius: 2px;
  background-color: #6cff8d;
}
.loader__text {
  position: relative;
  padding: 1rem;
  font-size: 20px;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
}
@media (max-width: 355px) {
  .loader__text {
    font-size: 17px;
  }
}
.loader__text--dot {
  width: 15px;
  height: 15px;
  margin: 0 3px;
  border-radius: 50%;
  background: #6cff8f;
  animation: pulse 1s infinite;
}
@keyframes pulse {
  from {
    background-color: #01460f;
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.loader__text--border {
  width: 85%;
  height: 1px;
  background-color: #03811a;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
.loader__counter {
  position: absolute;
  top: 70%;
  left: 0;
  color: #fff;
  font-size: 20px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}
.loader__counter--number {
  color: #6cff8f;
}
.loader__width_animation {
  animation: width_animation 1s forwards;
}
@keyframes width_animation {
  0% {
    width: 85%;
  }
  100% {
    width: 250px;
  }
}
.loader__svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  opacity: 0;
}